import { Teacher } from './teacher';
import { Level } from './level';
import { Department } from './department';
import { Budget } from './budget';
import { Hour } from './hour';
import { Schedule } from './schedule';
import { Group } from './group';

// 
export class Contest {
  comid: number;//id
  comname: string;//
  teacher: Teacher;//
  unit: string;//()
  level: Level;//
  phone: string;//
  email: string;//
  department: Department;//
  date: string;//
  host: string;//
  time: string;//
  place: string;//
  introduce: string;// TODO ?
  object: string;//
  people: number;//
  sponsor: string;//
  plan: string;//
  tgoal: string;// TODO tGoal
  wgoal: string;// TODO wGoal
  sopinion: string;// TODO sOpinion
  slsign: string;//() TODO SLSign
  slsdate: string;// TODO SLSDate
  uopinion: string;// TODO uOpinion
  dsign: string;//() TODO dSign
  dsdate: string;// TODO dSDate
  tdopinion: string;// TODO tDOpinion
  olsign: string;// TODO oLSign
  olsdate: string;// TODO oLSDate
  lsign: string;//() TODO lSign
  lsdate: string;// TODO lSDate
  status: number;// (0 1. 23)
  applyBeginDate: string;//
  applyEndDate: string;//
  comDate: string;//
  // budget: Budget[];//
  // hour: Hour[];//
  // schedule: Schedule[];//
  // groups: Group[];//
  // guideTeachers: Teacher[];//
  constructor(comid: number, comname: string, teacher: Teacher, unit: string,
    level: Level, phone: string, email: string, department: Department,
    date: string, host: string, time: string, place: string, introduce: string,
    object: string, people: number, sponsor: string, plan: string,
    tgoal: string, wgoal: string, sopinion: string, slsign: string,
    slsdate: string, uopinion: string, dsign: string, dsdate: string,
    tdopinion: string, olsign: string, olsdate: string, lsign: string,
    lsdate: string, status: number,
    applyBeginDate: string, applyEndDate: string, comDate: string) {
    this.comid = comid;
    this.comname = comname;
    this.teacher = teacher;
    this.unit = unit;
    this.level = level;
    this.phone = phone;
    this.email = email;
    this.department = department;
    this.date = date;
    this.host = host;
    this.time = time;
    this.place = place;
    this.introduce = introduce;
    this.object = object;
    this.people = people;
    this.sponsor = sponsor;
    this.plan = plan;
    this.tgoal = tgoal;
    this.wgoal = wgoal;
    this.sopinion = sopinion;
    this.slsign = slsign;
    this.slsdate = slsdate;
    this.uopinion = uopinion;
    this.dsign = dsign;
    this.dsdate = dsdate;
    this.tdopinion = tdopinion;
    this.olsign = olsign;
    this.olsdate = olsdate;
    this.lsign = lsign;
    this.lsdate = lsdate;
    this.status = status;
    this.applyBeginDate = applyBeginDate;
    this.applyEndDate = applyEndDate;
    this.comDate = comDate;
  }
}



